import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import { useMappedImages } from "../../hooks/images";
import Img from "gatsby-image";

export default function Badges({ onGalleryImageClick }) {
  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: {
          relativePath: {
            in: [
              "projects/promotional-assets/blackfriday_badge1.png"
              "projects/promotional-assets/blackfriday_badge2.png"
              "projects/promotional-assets/blackfriday_badge3.png"
              "projects/promotional-assets/blackfriday_badge4.png"
              "projects/promotional-assets/blackfriday_badge5.png"
              "projects/promotional-assets/blackfriday_badge6.png"
            ]
          }
        }
      ) {
        ...FluidImagesFragment
      }
    }
  `);
  const optimizedImages = useMappedImages(data.allFile);

  return (
    <div className="mt-6">
      <p>Product badges</p>
      <div className="smooth-scrool overflow-x-scroll flex md:justify-center p-2 md:p-0 bg-gray-100 md:bg-transparent border md:border-none border-gray-200 md:border-transparent rounded-full">
        {[
          "projects/promotional-assets/blackfriday_badge1.png",
          "projects/promotional-assets/blackfriday_badge2.png",
          "projects/promotional-assets/blackfriday_badge3.png",
          "projects/promotional-assets/blackfriday_badge4.png",
          "projects/promotional-assets/blackfriday_badge5.png",
          "projects/promotional-assets/blackfriday_badge6.png",
        ].map((image) => {
          return (
            <div
              className="mr-4 w-24 flex-shrink-0"
              onClick={() => {
                onGalleryImageClick(image);
              }}
            >
              <Img
                className={"cursor-zoom-in"}
                fluid={optimizedImages[image].childImageSharp.fluid}
              />
            </div>
          );
        })}
      </div>
    </div>
  );
}
